html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-decor {
  width: 1.54em;
  height: 1em;
  fill: #00AAEF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 1.53em;
  height: 1em;
  fill: #8EB8C9;
}

body {
  background: #FFF;
  font-family: "Ubuntu",sans-serif;
}

.textual {
  font-size: 16px;
  line-height: 1.5;
  color: #000;
}

.textual h2 {
  font-size: 32px;
  line-height: 1.1;
  font-weight: normal;
  margin-bottom: 1em;
}

.textual p {
  margin-bottom: 1em;
}

.textual ol {
  list-style: decimal;
  position: relative;
  left: -5px;
}

@media only screen and (max-width: 720px) {
  .textual ol {
    margin-left: 2em;
  }
}

.textual ol li {
  padding-left: 5px;
}

.textual ol, .textual ul {
  margin-bottom: 1em;
}

.textual_big {
  font-size: 24px;
  line-height: 1.3;
}

.textual_big a {
  text-decoration: none;
  color: #000;
}

.form__field {
  margin-bottom: 10px;
}

.input, .textarea {
  text-align: center;
  line-height: normal;
  height: 52px;
  background: #eeeeee;
  font-weight: 600;
  color: #393939;
  text-transform: uppercase;
  width: 100%;
}

.input:placeholder, .textarea:placeholder {
  font-size: 14px;
}

.textarea {
  height: 100px;
  padding: 20px;
  border: none;
}

.button {
  font-size: 14px;
  line-height: 52px;
  height: 52px;
  display: block;
  background: #1cabec;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  width: 100%;
}

.button_inline {
  display: inline-block;
  padding: 0 60px;
  width: auto;
}

.container {
  margin: 0 auto;
  max-width: 940px;
  *zoom: 1;
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 980px) {
  .container {
    margin: 0 20px;
  }
}

.hero {
  background-size: cover;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  position: relative;
}

@media only screen and (max-width: 720px) {
  .hero:before {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    content: "";
  }
}

.hero .container {
  position: relative;
  height: 100vh;
  min-height: 500px;
}

.header {
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
}

@media only screen and (max-width: 720px) {
  .header {
    top: 30px;
  }
}

.header__logo {
  position: absolute;
  top: -46px;
  left: 50%;
  transform: translate(-50%, 0);
}

@media only screen and (max-width: 720px) {
  .header__logo {
    left: 0;
    transform: none;
    top: 0;
  }
}

.header__logo .icon {
  font-size: 80px;
  fill: #fff;
}

@media only screen and (max-width: 720px) {
  .header__logo .icon {
    font-size: 60px;
  }
}

.header__phone {
  float: left;
  text-decoration: none;
  color: #fff;
  opacity: 0.6;
}

@media only screen and (max-width: 720px) {
  .header__phone {
    float: right;
    margin-bottom: 10px;
    padding-top: 10px;
  }
}

.header__call {
  float: right;
  text-decoration: none;
  color: #fff;
  border-bottom: 1px dashed #fff;
  opacity: 0.6;
  text-transform: uppercase;
}

@media only screen and (max-width: 720px) {
  .header__call {
    clear: right;
  }
}

.nav {
  position: absolute;
  bottom: 60px;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media only screen and (max-width: 720px) {
  .nav {
    position: static;
    display: block;
    padding-top: 200px;
  }
}

.nav a {
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

@media only screen and (max-width: 720px) {
  .nav li {
    margin-bottom: 10px;
    text-align: center;
    font-size: 25px;
  }
}

.wrapper {
  background: #fff;
  position: relative;
  z-index: 200;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

body {
  padding-bottom: 540px;
}

.footer {
  height: 540px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  position: fixed;
  bottom: 0;
  z-index: 100;
  left: 0;
  right: 0;
}

.footer__logo {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 78px;
}

.footer__logo .icon {
  fill: #fff;
}

.footer__copy {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  font-size: 16px;
  bottom: 66px;
  left: 0;
  right: 0;
  position: absolute;
}

.section {
  padding: 130px 0;
  position: relative;
}

@media only screen and (max-width: 760px) {
  .section {
    padding: 60px 0;
  }
}

.section__bottom {
  text-align: center;
  margin-top: 30px;
}

.section:nth-child(odd) {
  background: #eeeeee;
}

.section__left {
  float: left;
  width: 50%;
}

@media only screen and (max-width: 720px) {
  .section__left {
    float: none;
    width: auto;
    margin-bottom: 30px;
  }
}

.section__right {
  float: right;
  width: 33%;
}

@media only screen and (max-width: 720px) {
  .section__right {
    float: none;
    width: auto;
  }
}

.section__title {
  position: absolute;
  left: 50%;
  top: -.5em;
  transform: translate(-50%, 0);
  font-size: 48px;
  font-weight: 100;
  color: #000;
  padding-bottom: 14px;
  text-transform: uppercase;
  line-height: 1;
}

@media only screen and (max-width: 600px) {
  .section__title {
    position: relative;
    top: 0;
    left: 0;
    transform: none;
    margin-bottom: 40px;
    font-size: 30px;
    text-align: center;
  }
}

.section__title .icon {
  position: absolute;
  bottom: 0;
  font-size: 13px;
  fill: #00a7eb;
  margin-bottom: -11px;
  left: 50%;
  transform: translate(-50%, 0);
}

.section__title:after, .section__title:before {
  content: "";
  width: calc(50% - 20px);
  position: absolute;
  height: 2px;
  background: #1cabec;
  bottom: 0;
}

.section__title:before {
  left: 0;
}

.section__title:after {
  right: 0;
}

.best__motto {
  float: left;
  width: 33%;
  font-size: 48px;
  text-transform: uppercase;
  font-weight: 100;
}

@media only screen and (max-width: 960px) {
  .best__motto {
    width: auto;
    float: none;
    margin-bottom: 20px;
  }
  .best__motto br {
    display: none;
  }
}

.best__motto strong {
  font-weight: 600;
}

.best__motto span {
  font-weight: 100;
  color: #1dabec;
}

.best__text {
  float: right;
  width: 66%;
}

@media only screen and (max-width: 960px) {
  .best__text {
    width: auto;
    float: none;
  }
}

.apart {
  margin-bottom: 30px;
}

.apart h2 {
  font-weight: 600;
  font-size: 16px;
}

.apart h2 span {
  font-weight: 300;
  color: #1cabec;
}

.apart__photos {
  display: flex;
  margin-bottom: 12px;
}

.apart__photo {
  height: 260px;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  flex: 1;
}

@media only screen and (max-width: 720px) {
  .apart__photo {
    height: 100px;
  }
}

.apart__photo:first-child {
  margin-right: 20px;
}

.apart_mod-right .apart__photo:first-child {
  flex: 2;
}

.apart_mod-left .apart__photo:last-child {
  flex: 2;
}

.testis {
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 930px) {
  .testis {
    display: block;
  }
}

.t {
  text-align: center;
  padding: 0 2%;
}

@media only screen and (max-width: 930px) {
  .t {
    margin-bottom: 30px;
  }
}

.t__photo {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  display: inline-block;
  position: relative;
}

.t__photo:before {
  content: " ";
  position: absolute;
  left: -35px;
  top: 50%;
  margin-top: -15px;
  width: 40px;
  height: 40px;
  background-position: 0 0;
  background-size: contain;
  background-image: url(data:image/png;base64,);
}

.t__name {
  margin-top: 10px;
  font-weight: bold;
  font-size: 16px;
  color: #000;
}

.t__position {
  font-style: italic;
  font-size: 16px;
  color: #696973;
  margin-top: 5px;
}

.t__descr {
  line-height: 1.37;
  font-size: 16px;
  text-align: center;
  color: #000;
  margin-top: 25px;
}
